.space-shooter {
  max-width: 400px;
  margin: 0 auto;
  font-family: 'Courier New', monospace;
  text-align: center;
  background: #000;
  padding: 1rem;
  border-radius: 8px;
  color: #fff;
}

.game-canvas {
  border: 2px solid #fff;
  background: #111;
  display: block;
  margin: 0 auto 1rem;
}

.game-over {
  background: #000;
  color: #fff;
  padding: 2rem;
  border-radius: 8px;
}

.game-over button {
  background: #4CAF50;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
}

.game-over button:hover {
  background: #45a049;
}

p {
  font-size: 0.8rem;
  margin: 0.5rem 0;
  color: #ccc;
}

@media (max-width: 500px) {
  .space-shooter {
    padding: 0.5rem;
  }
  .game-canvas {
    width: 100%;
    height: auto;
  }
}
